
:root {
    --thm-font: 'Barlow Condensed', sans-serif;
    --heading-font: 'Charmonman', cursive;
    --thm-base: #ffa801;
    --thm-base-rgb: 255, 168, 1;
    --thm-primary: #40b9eb;
    --thm-primary-rgb: 64, 185, 235;
    --thm-secondary: #ff0143;
    --thm-secondary-rgb: 255, 1, 67;
    --thm-black: #082740;
    --thm-black-rgb: 8, 39, 64;
    --thm-gray: #eceeef;
    --blue: #007bff;
    --indigo: #6610f2;
    --purple: #6f42c1;
    --pink: #e83e8c;
    --red: #dc3545;
    --orange: #fd7e14;
    --yellow: #ffc107;
    --green: #28a745;
    --teal: #20c997;
    --cyan: #17a2b8;
    --white: #fff;
    --gray: #6c757d;
    --gray-dark: #343a40;
    --primary: #007bff;
    --secondary: #6c757d;
    --success: #28a745;
    --info: #17a2b8;
    --warning: #ffc107;
    --danger: #dc3545;
    --light: #f8f9fa;
    --dark: #343a40;
    --breakpoint-xs: 0;
    --breakpoint-sm: 576px;
    --breakpoint-md: 768px;
    --breakpoint-lg: 992px;
    --breakpoint-xl: 1200px;
    --font-family-sans-serif: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
    --font-family-monospace: SFMono-Regular,Menlo,Monaco,Consolas,"Liberation Mono","Courier New",monospace;
}
body {
    font-family: var(--thm-font);
    color: #7c859b;
    font-size: 18px;
    line-height: 34px;
    font-weight: 500;
}
.page-wrapper{
    position: relative;
    width: 100%;
    min-width: 300px;
    margin: 0px auto;
    overflow: hidden;
}
.topbar-one {
    background-color: #7d0004;
}
.topbar-one .container-fluid {
    padding-left: 0;
    padding-right: 0;
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    justify-content: space-between;
}
.topbar-one__left {
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    padding-left: 60px;
}
.topbar-one__right {
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
}
.main-nav__right {
    margin-left: auto;
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    padding-right: 60px;
}
.topbar-one__left a {
    color: #F5F5F5 !important;
    text-decoration: none;
}
 

.main-nav__search {
    margin-left: 20px;
    font-size: 21px;
    color:white;
    text-decoration: none;
    margin-right: 0px;
}
.main-nav__login, .main-nav__search {
    color: #fff;
    margin-right: 0;
    margin-left: 30px;
    font-size: 21px;
    text-decoration: none;
}
.main-nav__login:hover, .main-nav__search:hover{
    color:red;
}

