.tour-one__single {
    position: relative;
    margin-bottom: 30px;
}


.tour-one__image {
    position: relative;
    background-color: var(--thm-black);
    overflow: hidden;
}

    .tour-one__image > img {
        width: 100%;
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1);
        -webkit-transition: opacity 500ms ease, -webkit-transform 500ms ease;
        transition: opacity 500ms ease, -webkit-transform 500ms ease;
        transition: transform 500ms ease, opacity 500ms ease;
        transition: transform 500ms ease, opacity 500ms ease, -webkit-transform 500ms ease;
    }

    .tour-one__image > a {
        width: 38px;
        height: 38px;
        background-color: rgba(var(--thm-black-rgb), .2);
        border-radius: 50%;
        text-align: center;
        line-height: 38px;
        font-size: 14px;
        color: #fff;
        position: absolute;
        top: 30px;
        right: 30px;
        z-index: 10;
        -webkit-transition: all 500ms ease;
        transition: all 500ms ease;
    }

.tour-one__single:hover .tour-one__image > img {
    opacity: 1;
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
}

.tour-one__single:hover .tour-one__image > a {
    background-color: #fff;
    color: var(--thm-secondary);
}


.tour-one__content {
    border-style: solid;
    border-width: 1px;
    border-color: rgb(236, 238, 239);
    background-color: rgb(255, 255, 255);
    padding-left: 50px;
    padding-right: 50px;
    padding-top: 40px;
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
}

.tour-one__single:hover .tour-one__content {
    box-shadow: 0px 20px 20px 0px rgba(0, 0, 0, 0.05);
}

.tour-one__stars {
    font-size: 16px;
    font-weight: 500;
    color: #555;
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    line-height: 1em;
    margin-bottom: 5px;
}

    .tour-one__stars i {
        font-size: 14px;
        color: var(--thm-base);
        margin-right: 10px;
    }

.tour-one__content p {
    margin: 0;
    font-size: 16px;
    font-weight: 500;
    color: #555;
    line-height: 1em;
    margin-top: 10px;
    margin-bottom: 30px;
}

    .tour-one__content p span {
        color: var(--thm-secondary);
        font-size: 22px;
        font-weight: 600;
    }


.tour-one__meta {
    margin: 0;
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    padding-top: 18px;
    padding-bottom: 25px;
    border-top: 1px solid rgb(236, 238, 239);
}

    .tour-one__meta li + li {
        margin-left: 20px;
    }

    .tour-one__meta li a {
        font-size: 16px;
        font-weight: 500;
        color: #555;
        -webkit-transition: all 500ms ease;
        transition: all 500ms ease;
    }

    .tour-one__meta li i {
        font-size: 14px;
        margin-right: 5px;
        -webkit-transition: all 500ms ease;
        transition: all 500ms ease;
    }

    .tour-one__meta li a:hover {
        color: var(--thm-black);
    }

        .tour-one__meta li a:hover i {
            color: var(--thm-primary);
        }


.tour-one__content h3 {
    margin: 0;
    font-family: var(--thm-font);
    color: var(--thm-black);
    font-weight: 600;
    line-height: 34px;
    font-size: 26px;
}


.tour-one__content h3 a {
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
    
    }

.tour-one__content h3 a:hover {
            color: var(--thm-primary);
        }

