/* css banner */
.page-header {
    position: relative;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center center;
    padding: 80.5px 0;
}

    .page-header::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: var(--thm-black);
        opacity: 0.3;
    }

    .page-header .container {
        position: relative;
        text-align: center;
    }

    .page-header h2 {
        margin: 0;
        color: #ffffff;
        font-weight: 600;
        font-size: 60px;
        font-family: var(--thm-base);
    }

.thm-breadcrumb {
    display: -webkit-box;
    display: flex;
    margin: 0;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    flex-wrap: wrap;
}

    .thm-breadcrumb li {
        display: -webkit-box;
        display: flex;
        text-transform: uppercase;
        font-size: 16px;
        font-weight: 600;
        color: #fff;
    }
.thm-breadcrumb li + li::before {
            content: '/';
            margin-left: 15px;
            margin-right: 15px;
        }

        .thm-breadcrumb li a,
        .thm-breadcrumb li span {
            color: inherit;
            -webkit-transition: all 500ms ease;
            transition: all 500ms ease;
        }

            .thm-breadcrumb li a:hover {
                color: var(--thm-base);
            }   
.page-header{
    background-image: url('./assets/image/anh2.jpg')  ;
}
