/* css paging */
.post-pagination {
    display: -webkit-box;
    display: flex;
    -webkit-box-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    align-items: center;
    flex-wrap: wrap;
}

    .post-pagination a {
        border-radius: 50%;
        background-color: var(--thm-gray);
        color: #555;
        font-size: 18px;
        font-weight: 600;
        text-align: center;
        padding: 0;
        display: -webkit-box;
        display: flex;
        -webkit-box-align: center;
        align-items: center;
        -webkit-box-pack: center;
        justify-content: center;
        width: 57px;
        height: 57px;
        -webkit-transition: all .4s ease;
        transition: all .4s ease;
    }

        .post-pagination a:hover {
            background-color: var(--thm-primary);
            color: #fff;
        }

        .post-pagination a.active {
            background-color: var(--thm-base);
            color: #fff;
            cursor: auto;
        }

        .post-pagination a + a {
            margin-left: 15px;
        }  

 /* tour sorter */

.tour-sorter-one {
    display: -webkit-box;
    display: flex;
    -webkit-box-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    align-items: center;
    margin-bottom: 30px;
}

    .tour-sorter-one h3 {
        font-family: var(--thm-font);
        color: var(--thm-black);
        font-size: 24px;
        font-weight: 600;
        margin: 0;
    }

.tour-sorter-one__right {
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
}

    .tour-sorter-one__right > a {
        display: -webkit-box;
        display: flex;
        -webkit-box-align: center;
        align-items: center;
        -webkit-box-pack: center;
        justify-content: center;
        text-align: center;
        width: 43px;
        height: 43px;
        border-style: solid;
        border-width: 2px;
        border-color: rgb(236, 238, 239);
        font-size: 16px;
        color: #555;
        margin-left: 10px;
        -webkit-transition: all 500ms ease;
        transition: all 500ms ease;
    }

        .tour-sorter-one__right > a.active,
        .tour-sorter-one__right > a:hover {
            background-color: var(--thm-base);
            border-color: var(--thm-base);
            color: var(--thm-black);
        }

